@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Modak&display=swap);
.grid {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.square {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.5px solid black;
  box-shadow: 5px 5px 20px rgba(16, 16, 50, 0.3);
  padding: 2px;
}

.circle {
  height: 45px;
  width: 45px;
  border: 1px solid black;
  border-radius: 70%;
  box-shadow: 5px 5px 20px rgba(16, 16, 50, 0.3);
}

.p1 {
  background-color: #f012be;
  animation: player-1 1.5s;
}

.p2 {
  background-color: #2ecc40;
  animation: player-2 1.5s;
}

@keyframes player-1 {
  from {
    background-color: rgb(0, 123, 255);
  }
  to {
    background-color: #f012be;
  }
}

@keyframes player-2 {
  from {
    background-color: rgb(0, 123, 255);
  }
  to {
    background-color: #2ecc40;
  }
}

.indicator {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  margin-right: 1rem;
  border-radius: 0.15rem;
}

/* 
  --desktop: 1200px;
  --laptop: 1024px;
  --tablet: 768px;
  --phone: 480px;
   */
*,
*::before,
*::after {
  margin: 0;
  font-family: "Grandstander", cursive;
  box-sizing: border-box;
  background-color: #f3f3f3;
}

.box{
  width: 90%;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
}

.title {
  font-family: "Modak", cursive;
  font-size: 3rem;
  position: absolute;
  margin-top: -3rem;
  margin-left: 3rem;
  text-shadow: 7px 7px 0px #d2d2d22e;
  animation: title 2s ease-in-out alternate infinite;
}

@keyframes title {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
  }
}

@media screen and (max-width: 500px) {
  #title-msg-container {
    font-size: 3.5rem !important ;
  }
}
